/**
 * RedirectPopup.js
 * Popup to inform users that we are redirecting
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Logos from 'assets/images/InsurerLogos';
import './RedirectPage.scss';

class RedirectPage extends Component {
  componentDidMount() {
    const { data, history } = this.props;
    if (!data) {
      history.push('/forbidden');
      return;
    }

    if (data.func) {
      setTimeout(data.func, 3000);
    }
  }

  render() {
    const { data } = this.props;

    try {
      return (
        <div className="redirect-page">
          <img src={Logos.square[data.productData.insurer_slug]} alt={`${data.productData.insurer_slug}.png`} />
          <p className="loading">{data && data.desc}</p>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

RedirectPage.defaultProps = {
  data: null,
};

RedirectPage.propTypes = {
  data: PropTypes.object,
  history: PropTypes.object.isRequired,
};

// eslint-disable-next-line
const mapStateToProps = state => ({
  data: state.redirect.data,
});


export default withRouter(connect(mapStateToProps)(RedirectPage));
